export default [	{		path:'/login',		name:'login',		meta:{			title:'登录'		},		component:()=>import('@/views/login/Login.vue')	},	{		path:'/register',		name:'register',		meta:{			title:'注册'		},		component:()=>import('@/views/login/Register.vue')	},	{		path:'/game',		name:'game',		meta:{			title:'游戏'		},		component:()=>import('@/views/game/Game.vue'),		alias:'/'	},	{		path:'/game/downloadList',		name:'downloadList',		meta:{			title:'手游下载'		},		component:()=>import('@/views/game/GameDownloadList.vue')	},	{		path:'/game/download/deatils',		name:'downloadDeatils',		meta:{			title:'游戏下载'		},		component:()=>import('@/views/game/GameDownloadDetails.vue')	},	{		path:'/game/list',		name:'gamelist',		meta:{			title:'新品推荐'		},		component:()=>import('@/views/game/GameList.vue')	},	{		path:'/game/history',		name:'gameHistory',		meta:{			title:'我玩过的'		},		component:()=>import('@/views/game/GameHistory.vue')	},	{		path:'/read',		name:'read',		meta:{			title:'阅读'		},		component:()=>import('@/views/read/Read.vue'),	},	{		path:'/read/list',		name:'readList',		meta:{			title:'新书精选'		},		component:()=>import('@/views/read/ReadList.vue'),	},	{		path:'/read/history',		name:'readHistory',		meta:{			title:'我看过的'		},		component:()=>import('@/views/read/ReadHistory.vue')	},	{		path:'/task',		name:'task',		meta:{			title:'任务'		},		component:()=>import('@/views/task/Task.vue'),	},	{		path:'/user',		name:'user',		meta:{			title:'我的'		},		component:()=>import('@/views/user/User.vue'),	},	{		path:'/user/money',		name:'money',		component:()=>import('@/views/user/Money.vue'),		children:[			{				path:'goldcoin',				name:'goldcoin',				component:()=>import('@/views/user/MoneyGoldcoin.vue')			},			{				path:'balance',				name:'balance',				component:()=>import('@/views/user/MoneyBalance.vue')			},			{				path:'foxgrass',				name:'foxgrass',				component:()=>import('@/views/user/MoneyFoxgrass.vue')			}		]	},	{		path:'/user/money/forward',		name:'forward',		meta:{			title:'零钱提现'		},		component:()=>import('@/views/user/Forward.vue')	},	{		path:'/user/money/historyforward',		name:'historyForward',		meta:{			title:'提现记录'		},		component:()=>import('@/views/user/HistoryForward.vue')	},	{		path:'/user/message',		name:'message',		meta:{			title:'消息'		},		component:()=>import('@/views/user/Message.vue')	},	{		path:'/user/share',		name:'share',		meta:{			title:'邀请好友'		},		component:()=>import('@/views/user/Share.vue'),		children:[			{				path:'shareindex',				name:'shareindex',				meta:{					title:'邀请'				},				component:()=>import('@/views/user/ShareIndex.vue')			},			{				path:'sharemoney',				name:'sharemoney',				meta:{					title:'现金奖励'				},				component:()=>import('@/views/user/ShareMoney.vue')			},			{				path:'sharecoin',				name:'sharecoin',				meta:{					title:'金币奖励'				},				component:()=>import('@/views/user/ShareCoin.vue')			}		]	},	{		path:'/user/share/details',		name:'shareDetails',		meta:{			title:'分享赚钱'		},		component:()=>import('@/views/user/ShareDetails.vue')	}]


